* {
  scroll-behavior: smooth;
}

.navbars {
  background-color: rgba(0, 0, 0, 0.826);
  height: 20vh;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.navmenu {
  width: 40%;
}

.menu {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
}

.menu li {
  color: white;
  font-size: 18px;
  cursor: pointer;
}

.menu a {
  text-decoration: none;
}

.menu li:hover {
  text-decoration: underline;
}

.heromain {
  width: 100%;
  height: 80vh;
  background: url(./images/back.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
}

.content {
  width: 100%;
  height: 80vh;
  background-color: rgba(0, 0, 0, 0.427);
  justify-content: center;
  align-items: center;
  display: flex;
  color: white;
  font-size: 30px;
  font-style: oblique;
  text-align: center;
}

.about {
  background-color: #eab676;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.aboutchip {
  margin: 20px 50px 0px 0px;
  width: 40%;
  border: 3px solid white;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.514);
  position: relative;
  left: 50px;
  transition: 0.2s ease-in;
}
.aboutchip:nth-child(1) {
  width: 43%;
}

.aboutchip:nth-child(2) {
  width: 43%;
}

.aboutchip:nth-child(3) {
  width: 90%;
}


.aboutchip:hover {
  transform: scale(0.9);
}

