.heading {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-content {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 30vh; */
}

.about-content p  {
  width: 90%;
  font-size: 20px;
}

.projects {
  padding-top: 50px;
  background-color: #eab676;
  display:flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: rgba(255, 255, 255, 0.514);
  display: flex;
  border: 2px solid black;
  border-radius: 30px;
  margin-bottom: 20px;
  width: 80%;
  cursor: pointer;
}

.card:hover {
  width: 85%;
  transition: 0.3s ease-in;
}

.card-image {
  width: 30%;
  height: 300px;
}

.card-image img {
  width: 100%;
  height: 100%;
  border-radius: 30px 0 0 30px;
}

.card-content {
  margin-left: 40px;
}

.footer {
  width: 100%;
  height: 10vh;
  background-color: rgba(0, 0, 0, 0.826);
  color: white;
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.developed a {
  color: white;
}

.contact {
  background-color: #eab676;
  display: flex;
  width: 100%;
}

.info {
  width: 70%;
  background-color: rgba(255, 255, 255, 0.514);
  padding-left: 20px;
}

.info a {
  text-decoration: none;
  color: black;
}

.eventdesc {
  display: flex;
  justify-content: center;
  align-items: center;
}